<template>
  <div class="hello">
    <p>{{ist}}</p>
   <div>
     <mt-progress v-bind:value="num" :bar-height="5"></mt-progress>
   </div>
    <!--<button @click="coin=(coin==4000?0:4000)">点击停止{{coin}}</button>-->
    <div id="box">
        <div id="coins" @click="aa">
            <div id="coins_before">| |</div>
            <div id="coins_after"></div>
        </div>
         <mt-swipe  :auto="0" ref="swiper" :animating="wws">
          <mt-swipe-item>1555</mt-swipe-item>
           <mt-swipe-item>2ss</mt-swipe-item>
           <mt-swipe-item>3</mt-swipe-item>
        </mt-swipe>
    </div>

  <!--<mt-radio-->
    <!--title="radio list"-->
    <!--v-model="value"-->
    <!--:options="['optionA', 'optionB', 'optionC']">-->
  <!--</mt-radio>-->

  </div>
</template>

<script>
import Vue from 'vue'
//import { Toast } from 'mint-ui'
//import { Indicator } from 'mint-ui';
//import "./ceshi.css";
//import { Progress } from 'mint-ui';
//import { Swipe, SwipeItem } from 'mint-ui';
//import { Radio } from 'mint-ui';
//Vue.component(Progress.name, Progress);
//Vue.component(Swipe.name, Swipe);
//Vue.component(SwipeItem.name, SwipeItem);
//Vue.component(Radio.name, Radio);

export default {
  name: 'coins',
  data () {
    return {
      num:0,
      int:null,
      coin:3000,
      items:["one","tow","thire"],
      wws:true
    }
  },
  props:{
    ist:{
      type: String,
      default: "zy"
    }
  },
  created(){

     this.int = setInterval(()=>{
            this.num++
      },20)
  },
  mounted(){
    console.log("---------------",this.$refs.swiper);
    this.$refs.swiper.animating = false;
  },
  methods:{

//    handleChange(index) {
//        console.log(index)
//        this.coin = 0
//    },
    aa:function () {
      console.log(this.$refs.swiper);
      if(this.$refs.swiper.animating){
        this.$refs.swiper.animating = false;
        document.getElementById("coins_after").style.display="block";
        document.getElementById("coins_before").style.display="none";
      }else{
        this.$refs.swiper.animating = true;
        document.getElementById("coins_after").style.display="none";
        document.getElementById("coins_before").style.display="block"
      }

//      console.log(document.getElementById("box"))
//
//        document.getElementById("box").mousedown();
//      document.getElementById("box").onmousedown = function () {
//        timer1 = new Date();
//      }
    }
  },

  watch:{
    num:function(val,oldval){
        if(val==100){
         clearInterval(this.int)
       }
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
.mint-swipe-item:nth-child(1){
  background: red
}
.mint-swipe-item:nth-child(2){
  background: blue
}
.mint-swipe-item:nth-child(3){
  background: salmon
}
.mint-swipe-indicator{
  width: 20px!important;
  height: 20px!important;
  background: yellow
}
#box{
  width: 100%;
  height: 200px;

}
ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



