<template>
  <div>
    <h3 v-if="show">我是头部{{show}}</h3>
    <div v-show="book">显示 取消</div>
    <div :class="[active,errors]" :style="styleObject">ssss</div>
    <input type="text" v-model="mobile" onkeyup="this.value = this.value.replace(/\D/g,'')"/>
    <div style="width: 100px;height: 100px;background: red;position: fixed;top: 0;z-index: 1000" @click="doss1">
      <button @click.stop="doss" style="position: fixed;top: 0;z-index: 100">我是按钮父元素------------</button>
    </div>


    <!--<mt-header fixed title="固定在顶部"></mt-header>-->

    <!--<mt-header title="标题过长会隐藏后面的内容啊哈哈哈哈">-->


    <!--</mt-header>-->
    <h1 class="mint-header-title">标题过长会隐藏后面的内容啊哈哈哈哈</h1>
    <ul>
      <li @click="sex('date1')" :class="{active:date1}" id="date1">
        赵岩
        <p>
        {{arr}}
        </p>
        <p @click="onp">
          更改
        </p>
      </li>
      <li @click="sex('date2')" :class="{active:date2}" id="date2">
        赵岩2
      </li>
    </ul>
  </div>
</template>
<script>
  import Vue from 'vue'
  import { Header } from 'mint-ui';

  Vue.component(Header.name, Header);
  export default{
    name:'m',
    data(){
      return{
        isTrue:true,
        isActive: true,
        error: null,
        active:'active',
        errors:'ssss',
        mobile:'12223',
        date1:true,
        date2:false,
        styleObject:{
          color:'red',
          fontSize:'13px'
        },
        arr:[1,2,3]
      }
    },

    props:{
      show:{
      }
    },
    methods:{
      doss:function () {
        alert(1)
      },
      doss1:function () {
        alert(11)
      },
     sex:function(index){
          console.log(index)
          if(index=="date1"){
                this.date2=false
              this.date1=true
          }else if(index=="date2"){
            this.date1=false
              this.date2=true
          }
     },
      onp:function () {

        this.$set(this.arr,3,"ss")
      }
    },
    computed:{
      classObject:function () {
        return{
          active:this.isActive && !this.error,
          'text-danger': this.error && this.error.type === 'fatal',

        }
      },



      book:function () {
        if(this.show=="yes"){
           return false
        }else{
          return true
        }

      }
    }
  }
</script>
<style>
  .active{
    color:coral;
  }
</style>



