<template>
  <div class="hi">
    <ul>
      <li class="list" @click="show(index)" v-for="(item,index) in items" :class="{active:active==index}">选项卡{{item}}</li>

    </ul>
    <div>
      <div class="list" style="width: 100%;text-align: center;">选项卡{{tab}}</div>

    </div>
  </div>
</template>

<script>


  export default {
    name: 'hi',
    data () {
      return {
        num:0,
        int:null,
        coin:3000,
        items:["one","tow","thire"],
        wws:false,
        items:[1,2,3],
        tab:1,
        active:0,

      }
    },
    methods:{
      show:function(index){
        console.log(index);
        this.active = index;
        this.tab = index+1
//        this.$set(this.tab,index,)
      }
    }

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .list{
    width: 30%;
    margin: 0;
    padding: 0;
    text-align: center;

  }
  .list.active{
    background: pink;
  }
  h1, h2 {
    font-weight: normal;
  }
  .mint-swipe-item:nth-child(1){
    background: red
  }
  .mint-swipe-item:nth-child(2){
    background: blue
  }
  .mint-swipe-item:nth-child(3){
    background: salmon
  }
  .mint-swipe-indicator{
    width: 20px!important;
    height: 20px!important;
    background: yellow
  }
  #box{
    width: 100%;
    height: 200px;

  }
  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



