<template>

      <div>
        <ul>
          <li style="width: 100%" v-for="(list,index) in lists" @click="aa(list,index)" :class="{active:actives==index}">{{list}}</li>
          <button @click="bb">确定</button>
        </ul>

      </div>
</template>
<script>
  export default{
    data(){
      return{
        box:'sssss',
        lists:['寄售','不寄售'],
        actives:0,
        show:false
      }
    },
    methods:{
      aa(list,index){
        this.actives = index;
        this.$emit('ee',list);

      },
      bb(){
        this.$emit('ff')
      }
    }
  }

</script>
<style scoped>
  #boxsss{
    position: relative;
    width: 100%;
    height: 100%;
  }
   #box{
    height: 100%;
    width: 100%;
    background: #000000;
    position: fixed;
    top: 0;
    right: 0;
    opacity: 0.5;
     z-index: 10;

  }
   .slide-fade-enter-active {
     transition: all 1s ease;
   }
   .slide-fade-leave-active {
     transition: all 1s ease;
   }
   .slide-fade-enter, .slide-fade-leave-active {
     transform: translateX(100%);
     opacity: 0;
   }
   #list{
     width: 70%;
     height: 100%;
     background: #fff;
     position: fixed;
     top:0;
     right: 0;
     z-index: 20;
   }



</style>



