<template>
  <div>
    <input type="text" v-model="searchCont" @keyup.enter="searchS(searchCont)">
    <div style="background: red" @click="searchS(searchCont)">搜索</div>
    <ul>
      <li v-for="list in data">
        <p v-if="list.keyword" @click="searchGetUrl(list)">{{list.keyword}}</p>
        <p v-if="list.filename" @click="searchGetUrl(list)">{{list.filename}}</p>
      </li>
    </ul>
    <playMusic :str="name"></playMusic>
  </div>
</template>
<script>
  import {mapState, mapGetters, mapActions} from 'vuex'
  import playMusic from "./playMusic.vue"
  export default{
    data(){
      return {
        data: "",
        searchCont: '',
        name: null,
      }
    },
    components: {
      playMusic
    },
    created(){
      this.getOrder({
        'plat': 1,
        'count': 30,
      }).then((res) => {
        console.log(res)
        this.data = res.body.data.info;
      })
    },
    methods: {
      searchGetUrl(key){
        if (key.keyword) {
          this.$http.get('/search/api/v3/search/song',
            {
              params: {
                keyword: key.keyword,
                page: 1,
                pagesize: 100,
                showtype: 1,
              }
            }
          ).then((res) => {
            console.log(res.body.data.info)
            this.name = res.body.data.info[0]
          })
        } else {
          this.name = key
        }

      },
      searchS(key){
        if (key) {
          this.searchCont = key;
        }
        var cont = this.searchCont;
        var page = 1;
        this.$http.get('/search/api/v3/search/song?keyword=' + cont + '&page=' + page + '&pagesize=100&showtype=1', {}).then(function (response) {
          console.log(response)
          this.data = response.body.data.info

        })
      },
      ...mapActions({
        getOrder: 'SERCH_B'
      }),
    }
  }
</script>



