<template>
  <div>
    {{show}}
    <button @click="cc">我是子元素{{shows}}</button>
    <div id="top"></div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
        scroll:0
      }
    },
    methods:{
      cc(){
       this.shows=false
      },
      menu(){
        this.scroll = document.body.scrollTop;
        console.log("2",this.scroll)

      }
    },
    mounted(){
      window.addEventListener('scroll', this.menu)
    },

    computed:{
      shows(){
        return this.show
      }
    },
    props:['show']
  }
</script>
<style scoped>
  #top{
    /*width: 100%;*/
    /*height: 2000px;*/
    /*background: salmon;*/
  }
</style>



