    </div>
</template>

<script>
  import hello from '../components/Hello.vue'
  import hi from '../components/hi.vue'
  export default {
    name:'ceshi',
    methods:{
      goIndex:function () {
        this.$router.push({path:"/index/5"})
      }
    },
    beforeEnter:(to,from,next)=>{
      next(vm=>{

        console.log('zheshi1',vm)
      })
    },
    created(){
      console.log(undefined||"ss")
      const attr = [];
      console.log(this.str)
      this.str.forEach(function (value,index,a) {
        console.log(value)
        attr.push(value)
      })
      console.log(attr)
    },
    components: {
      "hello": hello,
      "hi":hi,
    },
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        book:'我是父元素过来的',
        str:[1,2,3,4]
      }
    },
  }
</script>

<style scoped>
#creat{
  width: 100%;
  height: 1000px;
  background: green;
}
  .router-link-active{
    background: salmon;

  }
  a{}
</style>



