import Vue from 'vue'
  function getHttp(){
    return new Vue().$http.get(
      '/api/mobile-goods-service/rs/purchaseData/getPriceConfig',
      {
        params: {
          gameId:"YX16053120241378200001",
           gameName:"地下城与勇士",
           regionName:"广东区",
           serverName:"广东1区"
        }
      },
      {
        headers: {
          contentType: "aplication/json; charset = UTF-8",
          dataType: 'json'
        }
      }).then(res=>{
          return res.body
    },()=>{
        console.log("kai guan cuo wu")
    })
  }
function sourceHttp(m){
  var Object ={};
  Object.mapRequest={keyword: m};
  Object.pageIndex=1;
  Object.pageSize=10;
  return new Vue().$http.post(
    'api/mobile-searchCenter-service/rs/gameSearch/searchKeyword',
        Object,

    {
      headers: {
        contentType: "aplication/json; charset = UTF-8",
        dataType: 'json'
      }
    }).then(res=>{
    return res.body
  },()=>{
    console.log("kai guan cuo wu")
  })
}
  function addss(m,n) {
    return (m+n)
  }
  export  {
  addss,
  getHttp,
  sourceHttp
};



