import Vue from 'vue';
const vm = new Vue();
export  default {
  state: {

  },
  mutations: {
    dlIndex(state, x){
      state.type = x.type
      state.index = x.index
    },
    SET_A(state,orderType){
      state.orderType = orderType
    },
    HELLO_A(state,id){
      state.id = id
    },
    sss(state,scroll){
      state.scroll = scroll
    },
    ooo(state,index){
      state.index = index
    }
  },
  actions: {
    GET_A({commit},box){
      return vm.$http.get('/api/mobile-category-service/rs/category/searchgamelistbytype',{params:box})
        .then((res)=>{
          console.log(res)
          commit('SET_A',res);
          return res
        })
    },
    HELLO({commit},top){
      return vm.$http.get('/api/mobile-category-service/rs/category/searchgoodstypebygameid',{params:top})
        .then((res)=>{
          console.log(res)
          commit('HELLO_A',res);
          return res
        })
    }

  }
  //   actions:{
  //     GET_A({commit},box){
  //       return new Promise((resolve,reject)=>{
  //         vm.$http.get('/api/mobile-category-service/rs/category/searchgamelistbytype',{params:box})
  //           .then((res)=>{
  //             commit('SET_A',res);
  //             resolve(res);
  //           })
  //       })
  //
  //     }
  //
  //   }









}







