<template>
  <div :style="isTr" class="box" id="demo">
    <div class="rank-banner" @click="ddd()">
      <img :src="rankBanner" alt="" style="height: 100px">
    </div>
    <ul style="height: 200px;background: red;overflow: scroll;list-style: none">
      <li style="height: 50px;background: black">1</li>
      <li style="height: 50px;background: salmon">1</li>
      <li style="height: 50px;background: black">1</li>
      <li style="height: 50px;background: salmon">1</li>
      <li style="height: 50px;background: black">1</li>
      <li style="height: 50px;background: black">1</li>
    </ul>
  </div>
</template>
<script>
  import {mapState, mapGetters, mapActions} from 'vuex'
  export default{
    name: 'plist',
    data(){
      return {
        list: [],
        rankBanner: "",
        isTr: ''
      }
