    created(){
      this.getOrder({
        'specialid': 23784,
        'page': 1,
        'json': true,
      }).then((res) => {
        console.log(res)
        var data = res.data;
        this.rankBanner = data.info.list.imgurl.replace('{size}', 400);
      })
    },
    methods: {
      getCss(){

      },
      ddd(){
        console.log(document.getElementById('demo').offsetTop)
        //this.isTr = 'overflow:hidden;height:'+(document.documentElement.clientHeight-document.getElementById('demo').offsetTop)+'px'

//        document.find('kugou-list').style.height = document.documentElement.clientHeight
//        document.body.style.overflow='hidden';
        setTimeout(function () {
          //document.getElementsByTagName('body')[0].style.overflow = "hidden"
          document.getElementsByTagName('body')[0].style.position = "fixed"
          document.getElementsByTagName('body')[0].style.width = "100%"
          //document.getElementsByTagName('body')[0].style.height = window.innerHeight+'px';
          //alert(document.getElementsByTagName('body')[0].style.height)
        }, 20);
      },
      ...mapActions({
        getOrder: 'SERCH_A'
      }),
    }

  }
</script>
<style>
  * {
    margin: 0;
    padding: 0;
  }

  .box {
    height: 2000px;
    background: #1b5bff;
  }

  .isTr {

  }

  li {
    list-style: none;
  }
</style>



