<template>
  <div>
    <input type="text" v-model="value">
    {{value}}
    <!--{{val}}-->

    <hr>
  </div>
</template>
<script>
export default{
  name:'custom',
  data () {
    return {
      value: this.values
    }
  },
  computed:{
    value:function(){
      return this.values
    }
  },
  props: ['values'],
  watch:{
    value:function(val,oldval){
        this.$emit("ee",val)
    }
  }
}
</script>



