import Vue from 'vue';
const vm = new Vue();
export default {
  state:{

  },
  mutations:{
    SET_B(state,githup){
      state.githup = githup
    },
  },
  actions:{
    GET_B({commit},box){
      return vm.$http.get('/api/mobile-category-service/rs/category/searchgamelistbytype',{params:box})
        .then((res)=>{
          console.log(res)
          commit('SET_B',res);
          return res
        })
    },
  }

}



