import vv from './boos/vv.vue'
import user from './boos/user.vue'
import aside from './boos/aside.vue'
import hh from './boos/hh.vue'
export const vvs ={
  path:'/vv',
  component:vv,
  children:[
    {
      path:'user',
      component:user,
      name:'user',
      meta:{
        keepAlive:false
      }
    },
    {
      path:'aside',
      component:aside,
      name:'aside',
      meta:{
        keepAlive:false
      }
    },
    {
      path:'hh',
      component:hh,
      name:'hh',
      meta:{
        keepAlive:false
      }
    }

  ]


}



