<template>
 <div>
   <ul>
     <li v-for="list in subNavs">
       {{list.name}}
       {{$route.params.name}}
     </li>
   </ul>
   <input type="radio" id="One" value="One" v-model="picked"/>
    <label for="One">One</label>
   <br>
   <input type="radio" id="Two" value="Two" v-model="picked"/>
   <label for="Two">Two</label>
   <br>
   <span>---{{list}}-----{{picked}}</span>
   <mt-switch v-model="value"></mt-switch>
   <div v-if="value" id="box" @click="hide"></div>
   <transition name="slide-fade">
     <div id="box1" v-if="value">
        <app @ee="ee" v-on:ff="ff"></app>
     </div>
   </transition>


   <p style="width: 100%;text-align: center;line-height: 30px" v-model="fass" @click="a">{{fass}}{{boxs.name}}{{box.name}}</p>
   <p style="width: 100%;text-align: center;line-height: 30px" v-model="fass2" @click="b">{{fass2}}{{boxs.name}}{{box.name}}</p>
   <p style="width: 100%;text-align: center;line-height: 30px" v-model="fass3" @click="c">{{fass3}}{{boxs.name}}{{box.name}}</p>

   <div slot="header">
     <transition name="fade">
       <div id="boxss" v-if="fass==true">

           <ul style="width: 50%;height: 100%;position: absolute;left: 0">
             <li @click="aaa()" style="position: relative">选择区
               <transition name="insss1">
                 <span v-if="isinline" style="width: 100%;height: 2px;position: absolute;left: 0;bottom: 0;" :class="{inline:isinline}" ></span>
               </transition>
             </li>
             <li @click="bbbs(list,index)" v-for="(list,index) in list2" :class="{active:actives==index}">{{list.name}}</li>
           </ul>

         <transition name="fades">
           <ul v-if="ggss" style="width: 50%;height: 100%;position: absolute;right: 0">
             <li @click="ccc()" style="position: relative">选择服
               <transition name="insss">
                 <span v-if="!isinline" style="width: 100%;height: 2px;position: absolute;left: 0;bottom: 0;" :class="{inline:!isinline}" ></span>
               </transition>
             </li>
             <li @click="bbb(list,index)" v-for="(list,index) in lists" :class="{active:active==index}">{{list.name}}</li>
           </ul>
         </transition>
       </div>

       <div id="box2" v-else-if="fass2==true">
         <ul>
           <li>部落</li>
           <li>联盟</li>
         </ul>
       </div>

       <div id="box3" v-else-if="fass3==true">
         <ul>
           <li>我要卖</li>
           <li>我要买</li>
           <li>我要买</li><li>我要买</li><li>我要买</li>

         </ul>

       </div>

     </transition>
   </div>


 </div>
</template>
<script>
  import adminAside from './aside.vue'
  import app from './app.vue'
  import Vue from 'vue'
  import { Switch } from 'mint-ui';
  Vue.component(Switch.name, Switch);
  export default {
    name:'user',
    components:{
      'app':app
    },
    data(){
      return{
          picked:'One',
        show:false,
        isinline:true,
        value:false,
        ggss:false,
        box:'',
        fass:false,
        fass2:false,
        fass3:false,
        active:0,
        boxs:'',
        actives:0,
        list:'',
        list2:[
          {
            name:'老用户',
          },
          {
            name:'信用户',
          }


        ],
        lists:[
          {
            name:'老用户',
          },
          {
            name:'老用户',
          },
          {
            name:'老用户',
          },
          {
            name:'老用户',
          },
          {
            name:'老用户',
          },
          {
            name:'老用户',
          },
          {
            name:'用户3',
          }

        ],
          subNavs:[
//            {
//              path:'/users',
//              name:'用户管理',
//              exact:true
//            },{
//              path:'/users/check',
//              name:'用户审核',
//              exact:false
//            },{
//              path:'/users/notify',
//              name:'用户通知',
//              exact:false
//            }

          ]
        }
    },
    methods:{
      hide(){

        this.value=false
      },
      bbb(list,index){
       this.active = index
        this.box = list

        //this.isinline = true

      },
      ee(a){
        this.list = a
      },
      ff(a){
       this.value=false
      },
      a(){
        this.fass2 = false
        this.fass3 = false
        if(this.fass == false){
          this.fass = true
        }else{
          this.fass = false
        }

      },
      b(){
        if(this.fass2 == false){
          this.fass2 = true
        }else{
          this.fass2 = false
        }
        this.fass = false
        this.fass3 = false
      },
      c(){
        if(this.fass3 == false){
          this.fass3 = true
        }else{
          this.fass3 = false
        }

        this.fass2 = false
        this.fass = false
      },
      aaa(){
        this.isinline = true
      },
      ccc(){
        this.isinline = false
      },
      bbbs(list,index){
        this.ggss = true
//        if(this.ggss==false ){
//          this.ggss = true
//        }else{
////          this.ggss = false
//        }
        this.isinline = false;
        if(index==0){
          this.lists=[
            {
              name:'新用户',
            },
            {
              name:'新用户',
            },
            {
              name:'新用户',
            },
            {
              name:'新用户',
            },
            {
              name:'新用户',
            },
            {
              name:'新用户',
            },
            {
              name:'新用户',
            }
          ]

        }else{
          this.lists=[
            {
              name:'老用户',
            },
            {
              name:'老用户',
            },
            {
              name:'老用户',
            },
            {
              name:'老用户',
            },
            {
              name:'老用户',
            },
            {
              name:'老用户',
            },
            {
              name:'老用户',
            }
          ]

        }

//        if(this.isinline==false ){
//          this.isinline = true
//        }else{
//          this.isinline = false
//        }
        this.actives = index
        this.boxs = list

      }
    },
    watch:{
      value(val,oldval){
        console.log(val)
        if(val==true){
            this.show = false
        }else {
          this.show = true
        }
      },
      picked(val,oldval){
        if(val == 'One'){
          console.log(1)
        }
      }
    }
  }
</script>
<style scoped>
  .active{
    color:red;
  }
  ul{
   list-style: none;
    overflow: auto;
  }
  .mint-switch:nth-child(1){
    float: left;
  }
  .mint-switch:nth-child(2){
    float: left;
  }
  .mint-switch:nth-child(3){
    float: left;
  }
  #box{
    height: 100%;
    width: 100%;
    background: #000000;
    position: fixed;
    top: 0;
    right: 0;
    opacity: 0.5;
  }
  #box1{
    height: 100%;
    width: 70%;
    background: #fff;
    position: fixed;
    top: 0;
    right: 0;

  }
  #boxss{
    height: 40%;
    width: 100%;
    background: #fff;
    position: fixed;
    bottom: 0;
    right: 0;

  }
  #boxss2{
    height: 40%;
    width: 100%;
    background: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    opacity: 1;
  }
  #boxss3{
    height: 40%;
    width: 100%;
    background: #fff;
    position: fixed;
    bottom: 0;
    right: 0;
    opacity: 1;
  }
  #boxss li{
    background: #fff;

  }
  .slide-fade-enter-active {
    transition: all 1s ease;
  }
  .slide-fade-leave-active {
    transition: all 1s ease;
  }
  .slide-fade-enter, .slide-fade-leave-active {
    transform: translateX(100%);
    opacity: 0;
  }
  .fade-enter-active {
    transition: all 1s ease;
  }
  .fade-leave-active {
    transition: all 1s ease;
  }
  .fade-enter, .fade-leave-active {
    transform: translateY(100%);
    opacity: 0;
  }

  .fades-enter-active {
    transition: all 1s ease;
  }
  .fades-leave-active {
    transition: all 1s ease;
  }
  .fades-enter, .fades-leave-active {
    transform: translateX(-50%);
    opacity: 0;
  }

  .insss1-enter-active {
    transition: all 1s ease;
  }
  .insss1-leave-active {
    transition: all 1s ease;
  }
  .insss1-enter,.insss1-leave-active{
    transform: translateX(100%);
    opacity: 0;
  }


  .insss-enter-active {
    transition: all 1s ease;
  }
  .insss-leave-active {
    transition: all 1s ease;
  }
  .insss-enter ,.insss-leave-active {
    transform: translateX(-100%);
    opacity: 0;
  }



  li{
    border-bottom: 2px solid #fff;
  }
  .inline{
    border-bottom: 2px solid #f75e46;
  }
</style>



