import Vue from 'vue'
import store from './stores'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'

import {CONFIG} from './assets/js/config/index.js'
Vue.use(CONFIG);

Vue.use(MintUI)
import indexName from './page/index.vue'
import hello from './components/Hello.vue'
import vuev from './components/src/swipe.vue'
import Creat from './page/Creat.vue'
import VueResource from 'vue-resource'
import Vuex from 'vuex'
Vue.use(Vuex)
Vue.use(VueResource)
import {ceshi} from './ceshi.js'
import {es} from './es6.js'
import {kugous} from './kugou.js'
import main from './page/main.vue'
import {buke} from './buke.js'
import {vvs} from './vv.js'
import {dd} from './child.js'
import {hbox} from './hh.js'
import Counter from './page/Counter.vue'
import Githup from './Githup.vue'
const router = new VueRouter({
    mode: 'history',
    base: __dirname,
    routes: [
      {path: '/', component: indexName,name: 'indexName',},
      {path: '/index/:name', component: indexName},
      {path: '/hello', component: hello,
      },
      {path: '/vue', component: vuev},
      {path: '/main', component: main},
      {path: '/counter', component: Counter},
      {path: '/githup', component: Githup},
      ceshi,
      buke,
      vvs,
      dd,
      hbox,
      es,
      kugous
    ]
  },

)
/* eslint-disable no-new */
var vm = new Vue({
  router,
  store,
  template: `
    <div>
      <transition name="fade" mode="out-in">
      <keep-alive>
       <router-view></router-view>
      </keep-alive>
      
           
      </transition>
       
    </div>
  `
}).$mount("#app");




