<template>
  <div>
    我是gitss
    <previewer :list="imageList" ref="previewer"></previewer>
    <div v-for="item in imageList">
      {{item}}
    </div>
  </div>
</template>
<script>
  import {mapState,mapGetters,mapActions} from 'vuex'
  import previewer from './previwer.vue' // 查看图片
  export default{
    components: {
      previewer,
    },
    data() {
      return{
        imageList:[],
      }
    },
    created(){

      this.getOrder(
        {
          gameType:1,
          pageIndex:1,
          pageSize:100}).then((res)=>{

          console.log(res)

        this.githup.body.categoryList.forEach((item,index)=>{
          this.imageList.push(item.gameImageUrl)
        })

      })
    },
    methods:{
      ...mapActions({
        getOrder:'GET_B'
      }),
    },
    computed:{
      ...mapState({
        githup: state => {
          return state.account.githup
        },
      }),

    }

  }

</script>



