import Creat from './page/Creat.vue'
import Vue from 'vue'
import left from './page/left.vue'
import center from './page/center.vue'
import right from './page/right.vue'

export const ceshi =
  {
    path:'/Creat',
    component:Creat,
    name:'ceshi',
    beforeEnter:(to,from,next)=>{

      let vm = new Vue();
      console.log('zheshi1----------',vm)
      next()
    },
    children:[
      {
        path:'left/:num',
        component:left,
        name:'left',
      },
      {
        path:'center/:num',
        component:center,
        name:'center',
      },
      {
        path:'right/:num',
        component:right,
        name:'right',
      }
    ]
  }




